<script>
  import { onMount } from "svelte";
  import Select from "svelte-select";
  import axios from "axios";

  import { countries } from "./countries";

  let statsApi = "https://thevirustracker.com/free-api?countryTotal=";
  let timelimeApi = "https://thevirustracker.com/free-api?countryTimeline=";

  let loading = true;

  let optionsList = countries;
  let selectedValue = null;
  let renderGraph = false;
  let renderStats = false;
  let activeCases = 0;
  let recoveredCases = 0;
  let newCasesToday = 0;
  let deathCases = 0;

  const getOptionLabel = option => option.name;
  const getSelectionLabel = option => option.name;

  async function handleSelect() {
    console.log(selectedValue);
    let [response, timelimeResponse] = await Promise.all([
      axios.get(`${statsApi}${selectedValue.value}`),
      axios.get(`${timelimeApi}${selectedValue.value}`)
    ]);

    let data = response.data.countrydata[0];
    renderStats = true;

    activeCases = data.total_active_cases;
    recoveredCases = data.total_recovered;
    deathCases = data.total_deaths;
    newCasesToday = data.total_new_cases_today;

    let timelineData = timelimeResponse.data.timelineitems;

    google.charts.load("current", { packages: ["corechart"] });
    google.charts.setOnLoadCallback(() => {
      let timeseries = [];

      let entries = Object.entries(timelineData[0]);

      entries.forEach(entry => {
        let datesplit = entry[0].split("/");

        timeseries.push([
          new Date(datesplit[2], datesplit[0] - 1, datesplit[1]),
          parseInt(entry[1].new_daily_cases),
          parseInt(entry[1].new_daily_deaths)
        ]);
      });

      renderGraph = true;
      var data = new google.visualization.DataTable();
      data.addColumn("date", "Date");
      data.addColumn("number", "New Cases");
      data.addColumn("number", "New Deaths");

      data.addRows(timeseries);

      let options = {
        title: "COVID-19",
        bar: { groupWidth: "25%" },
        colors: ["orange", "#bada55", "#000"],
        curveType: "function",
        legend: { position: "bottom" },
        hAxis: { format: "MMM d, y" }
      };

      let chart = new google.visualization.LineChart(
        document.getElementById("curve_chart")
      );

      chart.draw(data, options);
    });
  }

  function handleClear() {}

  async function loadData() {
    // let request = console.log(countries);
    loading = false;
  }

  onMount(async () => {
    await loadData();
  });
</script>

<style>
  h1 {
    color: #ff3e00;
    text-transform: uppercase;
    font-size: 4em;
    font-weight: 100;
  }

  #curve_chart {
    height: 700px;
  }
</style>

<div class="container-fluid">
  <div class="row">
    <div class="col-12 text-center">
      <h1>COVID-19 by State</h1>
    </div>
  </div>

  <div class="row">
    <div class="col-xl-3 col-lg-3 col-md-12 mx-auto">
      <Select
        items={optionsList}
        placeholder="Search by Country/Province/State"
        bind:selectedValue
        on:select={handleSelect}
        on:clear={handleClear} />
    </div>
  </div>

  {#if loading}
    <div class="row">
      <div class="col-12 text-center">
        <h3>Loading..</h3>
      </div>
    </div>
  {:else if renderStats}
    <div class="row">
      <div class="col-xl-7 col-lg-7 col-md-12 mx-auto text-center">
        <div class="row mx-auto">

          <div class="col-xl-6 col-lg-6 col-md-6 mt-3">
            <strong>Active Cases: {activeCases}</strong>
          </div>

          <div class="col-xl-6 col-lg-6 col-md-6 mt-3">
            <strong>New Cases Today: {newCasesToday}</strong>
          </div>
        </div>

        <div class="row mx-auto">
          <div class="col-xl-6 col-lg-6 col-md6 mt-3">
            <strong>Recovered: {recoveredCases}</strong>
          </div>
          <div class="col-xl-6 col-lg-6 col-md6 mt-3">
            <strong>Deaths: {deathCases}</strong>
          </div>
        </div>
      </div>

      <div class="col-12 mx-auto">
        <div id="curve_chart" />
      </div>

    </div>
  {/if}

</div>
